import React from "react";

import '../css/favorite.css';

import {
  BrowserRouter as Router,
  withRouter,
  Switch,
  Route,
  Link
} from "react-router-dom";

class NavBar extends React.Component {

  render() {
    return (
      	<div>
  	      	<ul id='navBar'>
  	            <li class='navDest'><Link class='navName' to='/About'>About</Link></li>
  	            <li class='navDest'><Link class='navName' to='/Favorite'>Just A Few of My Favorite Things</Link></li>
  	            <li class='navDest'><Link class='navName' to='/Contact'>Contact</Link></li>
  	        </ul>
  	    </div>
    );
  }
}

export default withRouter(NavBar);