import React from "react";

import '../css/about_me.css';

//import './like_button.js';

import cahal from "../pics/cahal-pech-mayan-ruins.jpg";

import me from "../pics/Me.jpg"

export default class About extends React.Component {

	//Javascript function which changes the 'my_lightbox' modal element display from the defautlt to 'block' so that it may be seen.
	openLightbox = () => {
            document.getElementById('my_lightbox').style.display = "block";
    }

    //Javascript function which returns the 'my_lightbox' modal display value back to the default 'none' when called.
    closeLightbox = () => {
        document.getElementById('my_lightbox').style.display = "none";
    }

  render() {
    return (
    	<div>
	    	<h1>About Me:</h1>
	        <div class="Jeffrey" id="like_button_container"></div>
	        <div>
	            <p>I am a person who has always wanted to dig deep into a topic and see how it everything ticks. 
	                My family comes from a long line of Cajuns living in rural Louisiana, so when my parents packed us up and moved up north to Michigan I saw it as a grand adventure. 
	                All the different people, places, and seasons, I just had to dive in and figure it all out. 
	                This of course translated well into school where my natural inquisitiveness allowed me to excel in every class I took. 
	                More importantly though, I was loving it the entire time.
	            </p>

	            
	            
	            <p>Moving back to Louisiana was just as much of a joy as I could learn just as much about where I came from, putting salt back in food instead of the road and joining wrestling teams instead of hockey. 
	                From here, I took a major in anthropology and career in archaeology after I graduated. 
	                This provided ample opportunity to discover more about the past and encounter new and interesting puzzles. 
	                The field was an unpredictable place full of challenges and puzzles that kept me mentally engaged and challenged my faculties. 
	                However, the constant lifestyle of going from middle of nowhere hotel to middle of nowhere hotel left me feeling isolated as well as purposeless.
	            </p>
	            
	            <p>Finally, I decided it was time to settle down and get some direction in my life. 
	                Boston is a thriving city filled to the brim with possibility, so I decided to make it my home. 
	                I required both mental rigor and stability in my new career path.
	                After making new friends in Boston I began to become fascinated with all of the things they could do with tech. 
	                I fiddled with Arduino’s, classes, and language libraries until I became the man that you see today.
	            </p>
	        </div>
	        
	        <h5>One of my earliest excavation sites: Cahal Pech</h5>
	        <a href="https://en.wikipedia.org/wiki/Cahal_Pech" target='_blank'>
	        	<img src={cahal} alt="Cahal Pech" class="picture" />
	        </a>
	        
	        <h5>A picture of me in Transylvania</h5>
	        <img id='lightboxImg' src={me} alt="It's mah face" class="picture" onclick={() => this.openLightbox()} />
	        
	        <div id="my_lightbox" class="modal" onclick={() => this.closeLightbox()}>
	            <div class="lightbox-content">
	                <span class="close" onclick={() => this.closeLightbox()}>&times;</span>
	                <img id="lightPic" src={me} alt="It's mah face" />
	            </div>
	        </div>
        </div>
    	);
	}
}